.mainDiv {
    padding-bottom: 15px;
    border-bottom: 4px solid rgb(100, 149, 214);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 110vh;
}

.slectDiv {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-bottom: 10px;
}

.slect {
    border-radius: 5px;
    box-shadow: 0px 10px 9px -5px rgba(66, 68, 90, 1);
    background-color: transparent;
    transition: border-bottom 0.3s ease;
    font-family: Oswald;
    font-size: 15px;
    text-align: center;
}

.dataDiv {
    display: flex;
    gap: 10px;
}

p {
    font-family: Oswald;
    font-size: 15px;
    margin: 5px
}

.simpleDatadiv {
    font-family: Oswald;
    font-size: 20px;
}

.red{
    color:red
}

.green{
    color:green
}

input {
    border: 3px solid transparent;
    border-radius: 5px;
    font-family: Oswald;
    font-size: 15px;
    color: black;
    text-align: center;
    box-shadow: 0px 10px 9px -5px rgba(66, 68, 90, 1);
    width: 170px;
    transition: border-color 1.25s, transform 1s
}

input:hover{
    border-bottom: 3px solid blue;
}


 .datepickerDiv {
     padding-top: 15px;
     padding-bottom: 20px;
     display: flex;
     justify-content: space-around;
 }

 .button {
     width: 25px;
     height: 25px;
     padding: 0;
     border: 0;
     background-color: transparent;
     border-radius: 5px;
     box-shadow: 0px 2px 5px 1px rgba(66, 68, 90, 1);
     transition: transform 0.5s;
 }

 .button:hover {
    transform: scale(1.2);
}

.icon {
    position: relative;
    z-index: -1;
    width: 100%;
    height: 100%;
}
    
.iconAdd {
    color: green;
}
    
.iconRemove {
    color: red;
    }

canvas{
    background-color: #5bcdf718;
    border-radius: 3%;
    box-shadow: 0px 10px 15px rgba(13, 5, 49, 0.522)
}




@media screen and (min-width: 320px) {
}

@media screen and (max-width: 767px) {
    .slect {
        width: 230px;
    }

    .chart {
        margin-top: 20px;
        margin-bottom: 20px;
        min-height: 200px;
    }

}

@media screen and (min-width: 768px) and (max-width: 1159px) {
    .mainDiv {
        width: 450px;
        margin-left: 260px;
    }

    .slect {
        width: 270px;
    }

    .slectDiv{
        z-index: 0;
    }

    .datepickerDiv{
        z-index: 0;
    }

    .chart {
        margin-top: 20px;
        margin-bottom: 20px;
        max-height: 300px;
    }

}

@media screen and (min-width: 1159px) {

    .mainDiv {
        width: 750px;
        margin-left: 330px;
    }

    .slect {
            width: 270px;
            font-size: 20px;
            height: 30px;

    }


    .datepickerDiv{
        display: flex;
        justify-content: space-around;
        gap: 10px;
        align-items: flex-end;
    }

    input{
        font-size: 20px;
        width: 220px;
        
    }

    .button {
        width: 29px;
        height: 29px;
        
    }


    .dataDiv{
        font-size: 20px;
    }

    .chart{
        margin-top: 20px;
        margin-bottom: 20px;
        max-height: 400px;
    }

}
